html, body {
  background: #17202a;
  min-width: 100%;
  min-height: 100%;
}

#canvas-container {
  width: 100%;
  height: 100%;
  position: fixed;
}

#bg {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

#switch-container {
  z-index: 1;
  position: absolute;
  top: 20px;
  left: 20px;
}

.switch {
  transform-style: preserve-3d;
  perspective: 500px;
  width: 3.5em;
  height: 2em;
  font-size: 17px;
  animation: 3s infinite toggle__animation;
  display: inline-block;
  position: relative;
}

.switch:before {
  content: "";
  filter: blur(20px);
  z-index: -1;
  background-color: #d8ff99;
  background-image: radial-gradient(at 21% 46%, #57d5db 0, #0000 50%), radial-gradient(at 23% 25%, #eb7a7c 0, #0000 50%), radial-gradient(at 20% 1%, #ba8af4 0, #0000 50%), radial-gradient(at 86% 87%, #75b9e6 0, #0000 50%), radial-gradient(at 99% 41%, #9aefe2 0, #0000 50%), radial-gradient(at 55% 24%, #64d887 0, #0000 50%);
  border-radius: 50px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #fdfefedc;
  border-radius: 30px;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.slider:before {
  content: "";
  background-color: #ff99fd;
  background-image: radial-gradient(at 81% 39%, #f49fce 0, #0000 50%), radial-gradient(at 11% 72%, #c3a7ec 0, #0000 50%), radial-gradient(at 23% 20%, #d9fe6d 0, #0000 50%);
  border-radius: 50%;
  width: 1.4em;
  height: 1.4em;
  transition: all .4s;
  position: absolute;
  bottom: .35em;
  left: .3em;
  box-shadow: inset 0 -10px 10px #0000002b, 0 -1px 15px -8px #00000017;
}

.input__check:checked + .slider {
  background-color: #17202a;
}

.input__check:checked + .slider:before {
  transform: translateX(1.5em);
}

@keyframes toggle__animation {
  0%, 100% {
    transform: translateY(-10px)rotateX(15deg)rotateY(-20deg);
  }

  50% {
    transform: translateY(0)rotateX(15deg)rotateY(-20deg);
  }
}

#contenido {
  display: none;
}

.croquis_2D {
  background: #17202a;
  min-height: 100%;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.croquis_2D img {
  object-fit: cover;
  padding-top: 8%;
}

#mobileInterface {
  pointer-events: none;
  z-index: 11;
  touch-action: manipulation;
  width: calc(100% - 20px);
  height: 100vh;
  position: fixed;
  top: auto;
  bottom: 120px;
  left: 10px;
}

#mobileInterface #joystickWrapper1 {
  pointer-events: auto;
  z-index: 12;
  touch-action: manipulation;
  background-color: #0000;
  width: 240px;
  height: 240px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.noSelect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
  -webkit-tap-highlight-color: transparent;
}
